import axios from 'axios'

export const getProductList = (page) => {
    const response = axios({
        method: 'GET',
        url: `http://localhost:8080/api/products?page=${page}&size=10`
    })
        .then(response => {
            return response.data
        })
        .catch(err => {
            console.log(err)
        })
    return response
}

export const getProduct = (code) => {
    const response = axios({
        method: 'GET',
        url: `http://localhost:8080/api/products/${code}`
    }).then(res => {
        return res.data
    }).catch(err => {
        console.log(err)
    })

    return response
}

export const createProduct = (newProduct) => {
    const response = axios({
        method: 'POST',
        url: 'http://localhost:8080/api/products',
        data: newProduct
    }).then(data => {
        return {
            success: true,
            message: 'Create product success'
        }
    }).catch(err => {
        return {
            success: false,
            message: err.response.data.message
        }
    })

    return response
}

export const deleteProduct = (code) => {
    const response = axios({
        method: 'DELETE',
        url: `http://localhost:8080/api/products/${code}`
    }).then(res => {
        console.log(res.data)
        return {
            success: true,
            message: 'Delete product success!'
        }
    }).catch(err => {
        console.log(err)
        return {
            success: false,
            message: 'Fail to delete product!'
        }
    })

    return response
}

export const updateProduct = (code, updatedProduct) => {
    const response = axios({
        method: 'PUT',
        url: `http://localhost:8080/api/products/${code}`,
        data: updatedProduct
    }).then(data => {
        console.log(data)
        return {
            success: true,
            message: 'Update product success'
        }
    }).catch(err => {
        console.log(err)
        return {
            success: false,
            message: 'Update product failed',
            err: err.response?.data?.message || "Unknown error"
        }
    })

    return response
}