import React, { useState } from 'react'
import { Button, Typography, notification, Form, Input } from 'antd'
import { useNavigate } from 'react-router-dom'
import TextArea from 'antd/lib/input/TextArea'
import { createProduct } from '../service/service'
const CreateProductForm = () => {
    const navigate = useNavigate()
    const [productData, setProductData] = useState({
        code: "",
        name: "",
        category: "",
        brand: "",
        type: "",
        description: ""
    })

    const onClickSubmit = () => {
        console.log(productData)
        if (!productData.name || !productData.category || !productData.code) {
            notification.open({
                message: 'Error',
                description: 'Fill all the required fields',
                type: 'error'
            })
        } else {


            productData.code = productData.code.toUpperCase()
            productData.brand = productData.brand.toUpperCase() === "NULL" ? null : productData.brand
            productData.type = productData.type.toUpperCase() === "NULL" ? null : productData.type
            productData.description = productData.description.toUpperCase() === "NULL" ? null : productData.description
            createProduct(productData).then(data => {
                if (data.success) {
                    notification.open({
                        message: 'Success',
                        description: data.message,
                        type: 'success'
                    })
                } else {
                    notification.open({
                        message: 'Error',
                        description: data.message,
                        type: 'error'
                    })
                }



            })

            setProductData({
                ...productData,
                code: "",
                name: "",
                category: "",
                description: "",
                brand: "",
                type: ""
            })
        }

    }

    const onNameChange = (e) => {
        const data = { ...productData, name: e }
        setProductData(data)
    }

    const onCodeChange = (e) => {
        const data = { ...productData, code: e }
        setProductData(data)
    }

    const onCategoryChange = (e) => {
        const data = { ...productData, category: e }
        setProductData(data)
    }

    const onBrandChange = (e) => {
        const data = { ...productData, brand: e }
        setProductData(data)
    }

    const onDescriptionChange = (e) => {
        const data = { ...productData, description: e }
        setProductData(data)
    }

    const onTypeChange = (e) => {
        const data = { ...productData, type: e }
        setProductData(data)
    }

    return (
        <div style={{ alignSelf: 'center', marginBottom: 10 }}>
            <Typography.Title>New Products</Typography.Title>
            <Typography>Type 'null' for null value</Typography>
            <Form labelCol={{ span: 24 }} wrapperCol={{ span: 24 }}
                style={{

                }}
            >
                <Form.Item label="Code *">
                    <Input
                        required
                        value={productData.code}
                        placeholder='Product code'
                        onChange={(e) => onCodeChange(e.target.value)}
                    />
                </Form.Item>
                <Form.Item label="Name *">
                    <Input
                        required
                        placeholder='Product name'
                        value={productData.name}
                        onChange={(e) => onNameChange(e.target.value)}
                    />
                </Form.Item>
                <Form.Item label="Category *">
                    <Input
                        required
                        value={productData.category}
                        placeholder='Product category'
                        onChange={(e) => onCategoryChange(e.target.value)}
                    />
                </Form.Item>
                <Form.Item label="Brand">
                    <Input
                        value={productData.brand}
                        placeholder='Product brand'
                        onChange={(e) => onBrandChange(e.target.value)}
                    />
                </Form.Item>
                <Form.Item label="Type">
                    <Input
                        value={productData.type}
                        placeholder='Product Type'
                        onChange={(e) => onTypeChange(e.target.value)}
                    />
                </Form.Item>
                <Form.Item label="Description">
                    <TextArea
                        value={productData.description}
                        maxLength={100}
                        placeholder='Product description'
                        onChange={(e) => onDescriptionChange(e.target.value)}
                    />
                </Form.Item>
                <Form.Item wrapperCol={{ span: 24 }}>
                    <Button type='primary' onClick={() => onClickSubmit()} block color='green'>Create new product</Button>
                </Form.Item>
                <Form.Item wrapperCol={{ span: 24 }}>
                    <Button type="danger" block onClick={() => navigate("/")}>
                        Back to listing
                    </Button>
                </Form.Item>
            </Form>
        </div>
    )
}

export default CreateProductForm