import React, { useState, useEffect } from 'react'
import { Table, Button, Modal, notification } from 'antd'
import { deleteProduct, getProductList } from './../service/service';
import { useNavigate } from 'react-router-dom';
const ProductTable = () => {
    const navigate = useNavigate()
    const [productList, setProductList] = useState([])
    const [isLoading, setIsLoading] = useState(true)
    const [totalItem, setTotalItems] = useState(0)
    useEffect(() => {
        getProductList(1).then(data => {
            setProductList(data.products)
            setIsLoading(false)
            setTotalItems(data.totalItems)
        })
    }, [])


    const tableColumn = [
        {
            title: 'ID',
            dataIndex: 'id',
            key: 'id',
        },
        {
            title: 'Code',
            dataIndex: 'code',
            key: 'code',
        },
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: 'Category',
            dataIndex: 'category',
            key: 'category',
        },
        {
            title: 'Brand',
            dataIndex: 'brand',
            key: 'brand',
        },
        {
            title: 'Type',
            dataIndex: 'type',
            key: 'type',
        },
        {
            title: 'Description',
            dataIndex: 'description',
            key: 'description',
        },
        {
            title: 'Action',
            key: 'action',
            render: (_, record) => <div>
                <Button type='primary' style={{ marginRight: 10 }} onClick={() => onClickUpdate(record)}>Update</Button>
                <Button type="primary" danger onClick={() => onClickDeleteProduct(record)}>Delete</Button>
            </div>,
        },
    ]

    let productData = []
    if (productList.length) {


        productData = productList.map(product => {
            return {
                key: product.id,
                id: product.id,
                code: product.code,
                name: product.name,
                category: product.category,
                brand: product.brand,
                type: product.type,
                description: product.description
            }
        })
    }


    const onClickNewProduct = () => {
        navigate("/create")
    }

    const onClickUpdate = (record) => {
        navigate("/update", { state: record })
    }

    const onClickDeleteProduct = (record) => {
        Modal.confirm({
            centered: true,
            okButtonProps: {
                type: 'primary',
                danger: true
            },
            title: `Are you sure deleting ${record.name}? This action can not be undone.`,
            onOk: () => {
                deleteProduct(record.code).then(response => {
                    if (response.success) {
                        notification.open({
                            message: 'Success',
                            description: response.message,
                            type: 'success'
                        })
                        getProductList().then(data => {
                            setProductList(data)
                        })
                    } else {
                        notification.open({
                            message: 'Fail',
                            description: response.message,
                            type: 'error'
                        })
                    }
                })
            }
        })
    }

    // if (isLoading) {
    //     return (
    //         <Typography.Title>Loading products, please wait...</Typography.Title>
    //     )
    // }

    return (
        <div>
            <Button type='primary' onClick={() => onClickNewProduct()}>Create new product</Button>
            <Table columns={tableColumn} dataSource={productData}
                loading={isLoading}
                rowKey="id"
                pagination={{
                    position: ['topCenter', 'bottomCenter'],
                    total: totalItem,
                    pageSize: 10,
                    onChange: (page) => {
                        setIsLoading(true)
                        getProductList(page).then(data => {
                            setProductList(data.products)
                            setTotalItems(data.totalItems)
                            setIsLoading(false)
                        })
                    }
                }}

            />

        </div>
    )
}

export default ProductTable